import React, {
  useState,
  ReactNode,
  FC
} from 'react';

export interface ButtonProps {
  children: ReactNode;
  onClick?: () => void;
  size?: 'xs' | 'sm' | 'md' | 'lg' | 'xl';
  style?: React.CSSProperties;
}

const Button: FC<ButtonProps> = ({
  children,
  size = 'sm',
  style,
  onClick
}: ButtonProps) => {

  const sizeValue =
    size === 'xs'
      ? {width: 79, height: 30}
      : size === 'sm' ? 94
        : size === 'md' ? 128
          : size === 'lg' ? 156
            : 188;

  const defaultStyles = {
    ...style,
    border: '1px solid #ccc',
    borderRadius: '15px',
    width: sizeValue
    } as React.CSSProperties;

  return (
    <button
      onClick={onClick}
      style={
        style
          ? style
          : defaultStyles
      }
    >
      {children}
    </button>
  )
}

export default Button;
